import Vue from 'vue'
/* global Vue */
import Router from 'vue-router';
import SetRebatePage from '@/components/userCenter/SetRebatePage';
Vue.use(Router);

export const router = new Router({
  routes: [
    {
      path: '/',
      name: 'rebatePage',
      component: SetRebatePage
    }
  ]
});



