<template>
  <div class="wrapper">
    <div class="item-minibar">
      <wxc-minibar
          :title="tabTitles[1].title"
          left-button=""
          background-color="#009ff0"
          text-color="#FFFFFF"></wxc-minibar>
    </div>
    <scroller class="scroller">
      <div class="lottery-content">
        <text class="lottery-text">第 </text>
        <wxc-button
            class="lottery-issue-button"
            :text="this.lotteryIssue === ''? '可选期数': this.lotteryIssue"
            type="white"
            size="medium"
            @wxcButtonClicked="selectLotteryIssueHandler('report-lotteryIssue')">
        </wxc-button>
        <text class="lottery-text"> 期</text>
        <!-- 期数 pop up -->
        <wxc-popover
            ref="report-lotteryIssue"
            :buttons="lotteryIssueList"
            :position="lotteryIssuePopoverPosition1"
            :arrowPosition="lotteryIssuePopoverArrowPosition1"
            @wxcPopoverButtonClicked="selectedLotteryIssue"
        ></wxc-popover>
      </div>
      <div class="download-content">
        <wxc-button
            class="download-button"
            text="拷贝表1超链接"
            type="blue"
            size="medium"
            @wxcButtonClicked="downloadTable1()"></wxc-button>
        <wxc-button
            class="download-button"
            text="拷贝表2超链接"
            type="blue"
            size="medium"
            @wxcButtonClicked="downloadTable2()"></wxc-button>
        <text class="label-text">注：拷贝链接后，请打开任意浏览器粘贴链接，文件会自动下载！</text>
      </div>
    </scroller>
  </div>
</template>
<script>
import {
  WxcButton,
  WxcMinibar,
  WxcPopover
} from 'weex-ui';
import Config from '../tabBarConfig';
// const dom = weex.requireModule('dom') || {}
// const stream = weex.requireModule('stream') || {};
const modal = weex.requireModule('modal') || {};
const clipboard = weex.requireModule('clipboard');
// const USERID = 1;

export default {
  components: {
    WxcMinibar,
    WxcButton,
    WxcPopover
  },
  props: {
    globalIP: {
      type: String,
      default: ''
    },
    USERID: {
      type: Number,
      default: 1
    },
    lotteryIssueList: {
      type: Array,
      // eslint-disable-next-line vue/require-valid-default-prop
      default: []
    }
  },
  data () {
    return {
      tabTitles: Config.tabIconFontTitles,
      lotteryIssue: '',
      lotteryIssuePopoverPosition1: { x: 310, y: 180 },
      lotteryIssuePopoverArrowPosition1: { pos: 'top', x: 50 },
      getLotteryIssueAPI: this.globalIP + 'dancecalendars', // 获取所有六合彩期数API
      getTableOneAPI: this.globalIP + 'buysomedrinks/fetchtable1',
      getTableTwoAPI: this.globalIP + 'buysomedrinks/fetchtable2',
      downloadTableURL: '',
      message: ''
    };
  },
  mounted: function () {
  },
  methods: {
    selectLotteryIssueHandler (ref = 'report-lotteryIssue') {
      this.$refs[ref].wxcPopoverShow();
      console.log(this.$refs[ref]);
    },
    selectedLotteryIssue (obj) {
      console.log(obj);
      this.lotteryIssue = this.lotteryIssueList[obj.index].text;
    },
    downloadTable1 () {
      const self = this;
      if (self.lotteryIssue === '') {
        modal.toast({
          message: `请输入期数`,
          duration: 1.0
        });
        return;
      }
      const param = '?dancenumber=' + parseInt(self.lotteryIssue) + '&user=' + self.USERID;
      self.downloadTableURL = self.getTableOneAPI + param;
      clipboard.setString(self.downloadTableURL);
      modal.toast({
        message: '已拷贝！',
        duration: 2.0
      });
    },
    downloadTable2 () {
      const self = this;
      if (self.lotteryIssue === '') {
        modal.toast({
          message: `请输入期数`,
          duration: 1
        });
        return;
      }
      const param = '?dancenumber=' + parseInt(self.lotteryIssue) + '&user=' + self.USERID;
      self.downloadTableURL = self.getTableTwoAPI + param;
      clipboard.setString(self.downloadTableURL);
      modal.toast({
        message: '已拷贝！',
        duration: 2.0
      });
    }
  }
};
</script>
<style scoped>
  .wrapper {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0
  }
  .item-minibar{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 100px;
  }
  .scroller {
    flex: 1;
    position: absolute;
    top: 100px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  /*用户期数DIV布局*/
  .lottery-content {
    height: 50px;
    margin: 20px 15px;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  .lottery-text {
    margin-right: 15px;
    margin-left: 15px;
    font-size: 20px;
  }
  /*提交按钮DIV布局*/
  .download-content {
    margin-top: 150px;
    height: 700px;
    align-items: center;
    justify-content: center;
  }
  .download-button {
    width: 300px;
    height: 80px;
    margin-bottom: 100px;
    max-width: 300px;
  }
  /*注释文本*/
  .label-text {
    margin-top: 25px;
    color: red;
    font-size: 20px;
    align-items: center;
    justify-content: center;
  }
</style>



