      <!--返点行-->
      <div class="rebate-content">
        <text class="username-text">返点率</text>
        <text style="color: red; margin-right: 20px">*</text>
        <wxc-stepper
          default-value="0"
          step="1"
          max="20"
          min="0"
          @wxcStepperValueIsMinOver="rebateRateIsMinOver"
          @wxcStepperValueIsMaxOver="rebateRateIsMaxOver"
          @wxcStepperValueChanged="setRebateRate"></wxc-stepper>
