<script>
import {
  WxcButton,
  WxcDialog,
  WxcGridSelect,
  WxcMinibar,
  WxcPanItem,
  WxcPopover,
  WxcTabPage,
  WxcStepper
} from 'weex-ui';
import Config from '../tabBarConfig';
import MakeNewRecordsData from '../makeNewRecords/data';
import MakeNewRecordsStyles from '../makeNewRecords/styles';
// const dom = weex.requireModule('dom') || {}
const stream = weex.requireModule('stream') || {};
const modal = weex.requireModule('modal') || {};
// const USERID = 1;

export default {
  components: {
    WxcMinibar,
    WxcButton,
    WxcPopover,
    WxcTabPage,
    WxcPanItem,
    WxcGridSelect,
    WxcDialog,
    WxcStepper
  },
  props: {
    globalIP: {
      type: String,
      default: ''
    },
    USERID: {
      type: Number,
      default: 1
    },
    lotteryIssueList: {
      type: Array,
      // eslint-disable-next-line vue/require-valid-default-prop
      default: []
    },
    userNameList: {
      type: Array,
      // eslint-disable-next-line vue/require-valid-default-prop
      default: []
    }
  },
  data () {
    return {
      pageRerender: true,
      dialogShow: false,
      confirmDialogContext: '',
      getLotteryIssueAPI: this.globalIP + 'dancecalendars', // 获取所有六合彩期数API
      getGuestAPI: this.globalIP + 'guests', // 获取所有玩家API
      getbolutusAPI: this.globalIP + 'bolutus/number', // 获取所有玩法API
      buysomedrinksAPI: this.globalIP + 'buysomedrinks/', // 获取所有玩法API
      allSelectedNumbersList: [],
      tabTitles: Config.tabIconFontTitles,
      /* style数据 */
      subTabTitles: MakeNewRecordsData.subTabTitles,
      subTabStyles: MakeNewRecordsStyles.subTabStyles,
      gridButtonStyles: MakeNewRecordsStyles.gridButtonStyles,
      gridNormalNumberStyles: MakeNewRecordsStyles.gridNormalNumberStyles,
      customStyles: MakeNewRecordsStyles.customStyles,
      /* 平特数据 */
      normalAndSpecialPlayList: MakeNewRecordsData.normalAndSpecialPlayList,
      normalAddSpecialZodiacPlayList: MakeNewRecordsData.normalAddSpecialZodiacPlayList,
      /* 平码数据 */
      normalNumberPlayList: MakeNewRecordsData.normalNumberPlayList,
      /* 特码数据 */
      bigOrSmailList: MakeNewRecordsData.bigOrSmailList,
      singleOrDoubleList: MakeNewRecordsData.singleOrDoubleList,
      familyOrWildList: MakeNewRecordsData.familyOrWildList,
      skyOrGroundList: MakeNewRecordsData.skyOrGroundList,
      tailBigOrSmailList: MakeNewRecordsData.tailBigOrSmailList,
      colorList: MakeNewRecordsData.colorList,
      zodiacList: MakeNewRecordsData.zodiacList,
      numberList: MakeNewRecordsData.numberList,
      ifPaidList: MakeNewRecordsData.ifPaidList,
      lotteryIssue: '',
      userName: '',
      userNameInputValue: '',
      userTotalMoney: '',
      rebateRate: 0,
      userTotalMoneyInputValue: '',
      userNamePopoverPosition: { x: 100, y: 280 },
      userNamePopoverArrowPosition: { pos: 'top', x: 100 },
      lotteryIssuePopoverPosition: { x: 310, y: 180 },
      lotteryIssuePopoverArrowPosition: { pos: 'top', x: 50 },
      currentSubTab: 0,
      // 选择大小
      selectedBigOrSmail: '',
      selectedBigOrSmailEng: '',
      ifSelectedBigOrSmail: false,
      // 选择单双
      selectedSingleOrDouble: '',
      selectedSingleOrDoubleEng: '',
      ifSelectedSingleOrDouble: false,
      // 选择家肖野肖
      selectedFamilyOrWild: '',
      selectedFamilyOrWildEng: '',
      ifSelectedFamilyOrWild: false,
      // 选择天肖地肖
      selectedSkyOrGround: '',
      selectedSkyOrGroundEng: '',
      ifSelectedSkyOrGround: false,
      // 选择特尾大小
      selectedTailBigOrSmail: '',
      selectedTailBigOrSmailEng: '',
      ifSelectedTailBigOrSmail: false,
      // 选择波路
      selectedColor: '',
      selectedColorEng: '',
      ifSelectedColor: false,
      // 选择生肖
      selectedZodiac: [],
      selectedZodiacEng: [],
      // 选择平码类型
      selectedNormalNumberGame: '',
      // 选择平特类型
      selectedNormalAndSpecialNumberGame: '',
      selectedNormalAddSpecialZodiacGame: '',
      zodiacLimit: 0,
      // 选择号码
      selectedNumbers: [],
      ifSelectedNumbers: false,
      // 选择是否付款
      selectedIfPaid: '',
      // 选择具体玩法
      selectedGameID: 0,
      selectedGameName: '',
      numberLimit: 0
    };
  },
  watch: {
  },
  mounted: function () {
    const self = this;
    this.$refs['wxc-tab-page'].setPage(0, null, false);
    self.currentSubTab = 0;
    // 初始化特码大小选择
    self.selectedBigOrSmail = '';
    self.selectedBigOrSmailEng = '';
    self.ifSelectedBigOrSmail = false;
    // 初始化特码单双选择
    self.selectedSingleOrDouble = '';
    self.selectedSingleOrDoubleEng = '';
    self.ifSelectedSingleOrDouble = false;
    // 初始化特码家野肖选择
    self.selectedFamilyOrWild = '';
    self.selectedFamilyOrWildEng = '';
    self.ifSelectedFamilyOrWild = false;
    // 初始化特码天地肖选择
    self.selectedSkyOrGround = '';
    self.selectedSkyOrGroundEng = '';
    self.ifSelectedSkyOrGround = false;
    // 初始化特码尾大小选择
    self.selectedTailBigOrSmail = '';
    self.selectedTailBigOrSmailEng = '';
    self.ifSelectedTailBigOrSmail = false;
    // 初始化特码波路选择
    self.selectedColor = '';
    self.selectedColorEng = '';
    self.ifSelectedColor = false;
    // 选择平码类型
    self.selectedNormalNumberGame = '';
    // 选择平特类型
    self.selectedNormalAndSpecialNumberGame = '';
    self.selectedNormalAddSpecialZodiacGame = '';
    // 初始化付款选择
    self.selectedIfPaid = '已付款';
    // 初始化具体玩法
    self.selectedGameID = 0;
    self.rebateRate = 0;
    self.selectedGameName = '';
    self.allSelectedNumbersList = '';
    self.numberLimit = 24;
    self.zodiacLimit = 6;
    // self.getUserNamelist();
  },
  methods: {
    getTailBigOrSmailAPIHelper (selectedTailBigOrSmail) {
      switch (selectedTailBigOrSmail) {
        case '特尾大':
          return '_gte=5';
        case '特尾小':
          return '_lt=5';
        default:
          return '';
      }
    },
    getZodiacAPIHelper (selectedZodiac) {
      // /bolutus/number?animal=pig&&animal=dog
      let zodiacAPI = '';
      selectedZodiac.forEach(item => {
        zodiacAPI += 'animal=' + item + '&';
      });
      return zodiacAPI;
    },
    // 获取guest姓名列表
    getMoreUserNamelist () {
      const self = this;
      const userGuestAPI = self.getGuestAPI + '?user=' + self.USERID;
      console.log('getMoreUserNamelist->', userGuestAPI);
      stream.fetch({
        method: 'GET',
        url: userGuestAPI,
        type: 'json',
        timeout: 30000
      }, function (response) {
        if (!response.ok) {
          self.userNameList = [''];
          modal.toast({
            message: '服务器链接出问题了, 无法获取历史玩家姓名！',
            duration: 3
          });
        } else {
          self.userNameList = response.data;
          console.log('this.userNameList->', self.userNameList);
        }
      });
    },
    // 获取所有玩法
    getSelectedNumbers () {
      const self = this;
      let selectedBigOrSmailAPI = '?bigorsmall=' + this.selectedBigOrSmailEng;
      let selectedFamilyOrWildAPI = '?domesticorwild=' + this.selectedFamilyOrWildEng;
      let selectedSkyOrGroundAPI = '?skyorground=' + this.selectedSkyOrGroundEng;
      let selectedSingleOrDoubleAPI = '?singleordouble=' + this.selectedSingleOrDoubleEng;
      let selectedTailBigOrSmailAPI = '?ending' + this.getTailBigOrSmailAPIHelper(this.selectedTailBigOrSmail);
      let selectedZodiacAPI = '?' + this.getZodiacAPIHelper(this.selectedZodiacEng);
      let selectedColorAPI = '?color=' + this.selectedColorEng;
      let selectedZodiacAndColorAPI = '?' + this.getZodiacAPIHelper(this.selectedZodiacEng) + 'color=' + this.selectedColorEng;
      let param = '';
      if (this.selectedBigOrSmail !== '') {
        param = selectedBigOrSmailAPI;
      } else if (this.selectedFamilyOrWild !== '') {
        param = selectedFamilyOrWildAPI;
      } else if (this.selectedSkyOrGround !== '') {
        param = selectedSkyOrGroundAPI;
      } else if (this.selectedSingleOrDouble !== '') {
        param = selectedSingleOrDoubleAPI;
      } else if (this.selectedTailBigOrSmail !== '') {
        param = selectedTailBigOrSmailAPI;
      } else if (this.selectedColor !== '' && this.selectedZodiac.length === 0) {
        param = selectedColorAPI;
      } else if (this.selectedColor === '' && this.selectedZodiac.length !== 0) {
        param = selectedZodiacAPI;
      } else if (this.selectedColor !== '' && this.selectedZodiac.length !== 0) {
        param = selectedZodiacAndColorAPI;
      }
      console.log('API call->', self.getbolutusAPI + param);
      stream.fetch({
        method: 'GET',
        url: self.getbolutusAPI + param,
        type: 'json',
        timeout: 30000
      }, function (response) {
        if (!response.ok) {
          self.allSelectedNumbersList = [];
          modal.toast({
            message: '服务器链接出问题了, 无法获取六合彩玩法对照表！',
            duration: 3
          });
        } else {
          self.allSelectedNumbersList = response.data;
          // self.confirmDialogContext = String(self.allSelectedNumbersList.numbers);
          self.confirmDialogContext = '姓名：' + self.userName + '\n' +
            '期数：' + self.lotteryIssue + '\n' +
            '玩法：' + self.selectedGameName + '\n' +
            '购买数字: ' + String(self.allSelectedNumbersList.numbers) + '\n' +
            '购买总金额：' + self.userTotalMoney + '\n' +
            '返点率：' + self.rebateRate + ' %' + '\n' +
            '是否付款：' + ((self.selectedIfPaid === '已付款') ? '是' : '否');
          self.dialogShow = true;
          console.log('allSelectedNumbersList->', self.allSelectedNumbersList.numbers);
        }
      });
    },
    setUsernameHandler (ref = 'find-username') {
      if (this.userNameList !== []) {
        this.$refs[ref].wxcPopoverShow();
      }
    },
    setLotteryIssueHandler (ref = 'wxc-popover2') {
      this.$refs[ref].wxcPopoverShow();
    },
    userNamePopoverClicked (obj) {
      console.log(obj);
      this.userName = this.userNameList[obj.index].text;
    },
    lotteryIssuePopoverClicked (obj) {
      console.log(obj);
      this.lotteryIssue = this.lotteryIssueList[obj.index].text;
    },
    userNameInput (e) {
      this.userNameInputValue = e.value;
    },
    userNameChange (e) {
      this.userName = e.value;
      this.userNameInputValue = '';
    },
    setRebateRate (e) {
      this.rebateRate = e.value;
      console.log('设置返点率：', this.rebateRate);
    },
    rebateRateIsMaxOver () {
      modal.toast({
        message: '超过最大值啦'
      });
    },
    rebateRateIsMinOver () {
      modal.toast({
        message: '超过最小值啦'
      });
    },
    userTotalMoneyInput (e) {
      this.userTotalMoneyInputValue = e.value;
    },
    userTotalMoneyChange (e) {
      this.userTotalMoney = e.value;
    },
    setSubTabPage (e) {
      this.currentSubTab = e.page;
      console.log(e.page);
      switch (e.page) {
        case 0:
          this.bigOrSmailList = MakeNewRecordsData.bigOrSmailList;
          this.tailBigOrSmailList = MakeNewRecordsData.tailBigOrSmailList;
          this.singleOrDoubleList = MakeNewRecordsData.singleOrDoubleList;
          this.familyOrWildList = MakeNewRecordsData.familyOrWildList;
          this.skyOrGroundList = MakeNewRecordsData.skyOrGroundList;
          this.colorList = MakeNewRecordsData.colorList;
          this.numberList = [];
          this.zodiacList = MakeNewRecordsData.zodiacList;
          this.userName = '';
          this.userNameInputValue = '';
          this.userTotalMoney = '';
          this.rebateRate = 0;
          this.userTotalMoneyInputValue = '';
          this.confirmDialogContext = '';

          // 初始化特码大小选择
          this.selectedBigOrSmail = '';
          this.selectedBigOrSmailEng = '';
          this.ifSelectedBigOrSmail = false;
          // 初始化特码单双选择
          this.selectedSingleOrDouble = '';
          this.selectedSingleOrDoubleEng = '';
          this.ifSelectedSingleOrDouble = false;
          // 初始化特码家野肖选择
          this.selectedFamilyOrWild = '';
          this.selectedFamilyOrWildEng = '';
          this.ifSelectedFamilyOrWild = false;
          // 初始化特码天地肖选择
          this.selectedSkyOrGround = '';
          this.selectedSkyOrGroundEng = '';
          this.ifSelectedSkyOrGround = false;
          // 初始化特码尾大小选择
          this.selectedTailBigOrSmail = '';
          this.selectedTailBigOrSmailEng = '';
          this.ifSelectedTailBigOrSmail = false;
          // 初始化特码波路选择
          this.selectedColor = '';
          this.selectedColorEng = '';
          this.ifSelectedColor = false;
          // 初始化生肖
          this.selectedZodiac = [];
          this.selectedZodiacEng = [];
          this.selectedNumbers = [];
          this.numberLimit = 24;
          this.zodiacLimit = 6;
          // 初始化付款选择
          this.selectedIfPaid = '已付款';
          // 初始化具体玩法
          this.selectedGameID = 0;
          this.selectedGameName = '';
          this.selectedNormalNumberGame = '';
          this.selectedNormalAndSpecialNumberGame = '';
          this.selectedNormalAddSpecialZodiacGame = '';
          break;
        case 1:
          this.bigOrSmailList = MakeNewRecordsData.bigOrSmailList;
          this.tailBigOrSmailList = MakeNewRecordsData.tailBigOrSmailList;
          this.singleOrDoubleList = MakeNewRecordsData.singleOrDoubleList;
          this.familyOrWildList = MakeNewRecordsData.familyOrWildList;
          this.skyOrGroundList = MakeNewRecordsData.skyOrGroundList;
          this.colorList = MakeNewRecordsData.colorList;
          this.numberList = [];
          this.zodiacList = MakeNewRecordsData.zodiacList;
          this.userName = '';
          this.userNameInputValue = '';
          this.userTotalMoney = '';
          this.rebateRate = 0;
          this.userTotalMoneyInputValue = '';
          this.confirmDialogContext = '';

          // 初始化特码大小选择
          this.selectedBigOrSmail = '';
          this.selectedBigOrSmailEng = '';
          this.ifSelectedBigOrSmail = false;
          // 初始化特码单双选择
          this.selectedSingleOrDouble = '';
          this.selectedSingleOrDoubleEng = '';
          this.ifSelectedSingleOrDouble = false;
          // 初始化特码家野肖选择
          this.selectedFamilyOrWild = '';
          this.selectedFamilyOrWildEng = '';
          this.ifSelectedFamilyOrWild = false;
          // 初始化特码天地肖选择
          this.selectedSkyOrGround = '';
          this.selectedSkyOrGroundEng = '';
          this.ifSelectedSkyOrGround = false;
          // 初始化特码尾大小选择
          this.selectedTailBigOrSmail = '';
          this.selectedTailBigOrSmailEng = '';
          this.ifSelectedTailBigOrSmail = false;
          // 初始化特码波路选择
          this.selectedColor = '';
          this.selectedColorEng = '';
          this.ifSelectedColor = false;
          // 初始化生肖
          this.selectedZodiac = [];
          this.selectedZodiacEng = [];
          this.selectedNumbers = [];
          this.numberLimit = 24;
