    getSelectedGameName () {
      if (this.selectedBigOrSmail !== '') {
        return '特码大小';
      } else if (this.selectedSingleOrDouble !== '') {
        return '特码单双';
      } else if (this.selectedFamilyOrWild !== '') {
        return '特码家野肖';
      } else if (this.selectedSkyOrGround !== '') {
        return '特码天地肖';
      } else if (this.selectedTailBigOrSmail !== '') {
        return '特码尾大尾小';
      } else if (this.selectedNormalNumberGame === '平码') {
        return '平码';
      } else if (this.selectedColor === '蓝波' && this.selectedZodiac.length === 0) {
        return '特码蓝波';
      } else if (this.selectedColor === '绿波' && this.selectedZodiac.length === 0) {
        return '特码绿波';
      } else if (this.selectedColor === '红波' && this.selectedZodiac.length === 0) {
        return '特码红波';
      } else if ((this.selectedColor !== '' && this.selectedZodiac.length !== 0) ||
        (this.selectedNumbers.length !== 0 && this.selectedNormalNumberGame === '' && this.selectedNormalAndSpecialNumberGame === '' && this.selectedNormalAddSpecialZodiacGame === '') ||
        (this.selectedZodiac.length !== 0 && this.selectedNormalNumberGame === '' && this.selectedNormalAndSpecialNumberGame === '' && this.selectedNormalAddSpecialZodiacGame === '')) {
        return '特碰';
      } else if (this.selectedNormalNumberGame === '二中二') {
        return '二中二';
      } else if (this.selectedNormalNumberGame === '三中三') {
        return '三中三';
      } else if (this.selectedNormalNumberGame === '四中四') {
        return '四中四';
      } else if (this.selectedNormalAndSpecialNumberGame === '五不中') {
        return '五不中';
      } else if (this.selectedNormalAndSpecialNumberGame === '八不中') {
        return '八不中';
      } else if (this.selectedNormalAndSpecialNumberGame === '十不中') {
        return '十不中';
      } else if (this.selectedNormalAndSpecialNumberGame === '平特特碰') {
        return '平特特碰';
      } else if (this.selectedNormalAddSpecialZodiacGame === '平特一肖') {
        return '平特一肖';
      } else if (this.selectedNormalAddSpecialZodiacGame === '平特二肖') {
        return '平特二肖';
      } else if (this.selectedNormalAddSpecialZodiacGame === '平特三肖') {
        return '平特三肖';
      } else if (this.selectedNormalAddSpecialZodiacGame === '平特四肖') {
        return '平特四肖';
      } else {
        return '';
      }
    },
    getGameValue () {
      if (this.selectedBigOrSmail !== '') {
        return this.selectedBigOrSmail;
      } else if (this.selectedSingleOrDouble !== '') {
        return this.selectedSingleOrDouble;
      } else if (this.selectedFamilyOrWild !== '') {
        return this.selectedFamilyOrWild;
      } else if (this.selectedSkyOrGround !== '') {
        return this.selectedSkyOrGround;
      } else if (this.selectedTailBigOrSmail !== '') {
        return this.selectedTailBigOrSmail;
      } else if (this.selectedColor !== '' || this.selectedNumbers.length !== 0 || this.selectedZodiac.length !== 0) {
        return '特碰';
      } else {
        return '';
      }
    },
