    },
    submitBtnHandler () {
      this.selectedGameName = this.getSelectedGameName();
      console.log('selectedGameName->', this.selectedGameName);
      // this.userTotalMoney = 100;
      if (this.userName === '') {
        modal.toast({
          message: `请输入玩家姓名！`,
          duration: 1.0
        });
      } else if (this.lotteryIssue === '') {
        modal.toast({
          message: `请输入期数`,
          duration: 1.0
        });
        // eslint-disable-next-line brace-style
      }
      else if (this.userTotalMoney === '') {
        modal.toast({
          message: `请输入总金额！`,
          duration: 1.0
        });
      } else if (this.selectedGameName === '') {
        modal.toast({
          message: `请输入具体玩法或数字！`,
          duration: 1.0
        });
      } else if (this.selectedGameName === '二中二' && this.selectedNumbers.length !== 2) {
        modal.toast({
          message: `具体玩法和对应数字不同！`,
          duration: 1.0
        });
      } else if (this.selectedGameName === '三中三' && this.selectedNumbers.length !== 3) {
        modal.toast({
          message: `具体玩法和对应数字不同！`,
          duration: 1.0
        });
      } else if (this.selectedGameName === '四中四' && this.selectedNumbers.length !== 4) {
        modal.toast({
          message: `具体玩法和对应数字不同！`,
          duration: 1.0
        });
      } else if (this.selectedGameName === '五不中' && this.selectedNumbers.length !== 5) {
        modal.toast({
          message: `五不中玩法和对应数字不同！`,
          duration: 1.0
        });
      } else if (this.selectedGameName === '八不中' && this.selectedNumbers.length !== 8) {
        modal.toast({
          message: `八不中玩法和对应数字不同！`,
          duration: 1.0
        });
      } else if (this.selectedGameName === '十不中' && this.selectedNumbers.length !== 10) {
        modal.toast({
          message: `具体玩法和对应数字不同！`,
          duration: 1.0
        });
      } else if (this.selectedGameName === '平特一肖' && this.selectedZodiac.length !== 1) {
        modal.toast({
          message: `具体玩法和对应数字不同！`,
          duration: 1.0
        });
      } else if (this.selectedGameName === '平特二肖' && this.selectedZodiac.length !== 2) {
        modal.toast({
          message: `具体玩法和对应数字不同！`,
          duration: 1.0
        });
      } else if (this.selectedGameName === '平特三肖' && this.selectedZodiac.length !== 3) {
        modal.toast({
          message: `具体玩法和对应数字不同！`,
          duration: 1.0
        });
      } else if (this.selectedGameName === '平特四肖' && this.selectedZodiac.length !== 4) {
        modal.toast({
          message: `具体玩法和对应数字不同！`,
          duration: 1.0
        });
      } else {
        // 修复bug userTotalMoney 不为零是rebateRate 不等于0
        if (this.rebateRate === '') {
          this.rebateRate = 0;
        }
        if (this.selectedNumbers.length === 0) {
          this.getSelectedNumbers();
        } else if (this.selectedNumbers.length !== 0) {
          this.confirmDialogContext = '姓名：' + this.userName + '\n' +
            '期数：' + this.lotteryIssue + '\n' +
            '玩法：' + this.selectedGameName + '\n' +
            '购买数字: ' + String(this.selectedNumbers) + '\n' +
            '购买总金额：' + this.userTotalMoney + '\n' +
            '返点率：' + this.rebateRate + ' %' + '\n' +
            '是否付款：' + ((this.selectedIfPaid === '已付款') ? '是' : '否');
          this.dialogShow = true;
          console.log('selectedNumbers->', this.selectedNumbers);
        }
      }
    },
    openDialog () {
      this.dialogShow = true;
    },
    wxcDialogCancelBtnClicked () {
      // 此处必须设置，组件为无状态组件，自己管理
      this.dialogShow = false;
      this.confirmDialogContext = '';
    },
    // 提交记录
    wxcDialogConfirmBtnClicked () {
      let numberOfDrink = 1;
      let drink1 = [];
      const self = this;
      if (this.selectedGameName === '特碰') { // 选择特碰玩法
        if (this.selectedNumbers.length === 0) { // 选择特碰其他玩法
          const allSelectedNumberArray = this.allSelectedNumbersList.numbers.split(',');
          numberOfDrink = allSelectedNumberArray.length;
          drink1 = this.allSelectedNumbersList.numbers;
        } else { // 选择特碰数字玩法
          numberOfDrink = this.selectedNumbers.length;
          drink1 = this.selectedNumbers;
        }
      } else if (this.selectedGameName === '特码大小' || this.selectedGameName === '特码单双' ||
        this.selectedGameName === '特码家野肖' || this.selectedGameName === '特码天地肖' || this.selectedGameName === '特码尾大尾小' ||
        this.selectedGameName === '特码蓝波' || this.selectedGameName === '特码绿波' || this.selectedGameName === '特码红波') { // 选择属性玩法
        numberOfDrink = 1;
        drink1 = this.allSelectedNumbersList.numbers;
      } else if (this.selectedNormalNumberGame === '平码' || this.selectedNormalAndSpecialNumberGame === '平特特碰') { // 选择平码或者平特一般玩法
        numberOfDrink = this.selectedNumbers.length;
        drink1 = this.selectedNumbers;
      } else if (this.selectedNormalNumberGame === '二中二' || this.selectedNormalNumberGame === '三中三' || this.selectedNormalNumberGame === '四中四' ||
        this.selectedNormalAndSpecialNumberGame === '五不中' || this.selectedNormalAndSpecialNumberGame === '八不中' ||
        this.selectedNormalAndSpecialNumberGame === '十不中') { // 选择平码或者平特属性玩法
        numberOfDrink = 1;
        drink1 = this.selectedNumbers;
      } else if (this.selectedGameName === '平特一肖' || this.selectedGameName === '平特二肖' ||
        this.selectedGameName === '平特三肖' || this.selectedGameName === '平特四肖') { // 选择平特玩法
        numberOfDrink = this.allSelectedNumbersList.numbers.split(',').length;
        drink1 = this.allSelectedNumbersList.numbers;
      }
      const unitprice = this.userTotalMoney / numberOfDrink;
      console.log('numberOfDrink->', numberOfDrink);
      console.log('unitprice->', unitprice);
      const body = JSON.stringify(
        {
          'drinkname': String(this.selectedGameName),
          'guestname': String(this.userName),
          'user': self.USERID,
          'rebate': parseInt(this.rebateRate),
          'dancenumber': parseInt(this.lotteryIssue),
          'numberofdrink': parseInt(numberOfDrink),
          'unitprice': parseFloat(unitprice),
          'totalamount': parseInt(this.userTotalMoney),
          'payload': {
            'drink1': drink1
          },
          'payloaddescription': '',
          'ispaid': (this.selectedIfPaid === '已付款')
        });
      console.log('body->', body);
      stream.fetch({
        method: 'POST',
        url: this.buysomedrinksAPI,
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        type: 'json',
        body: body
      }, function (ret) {
        if (!ret.ok) {
          modal.toast({
            message: '网络出问题了, 无法发送信息！',
            duration: 3
          });
        } else {
          modal.toast({
            message: '提交成功!',
            duration: 6
          });
          /* 平特数据 */
          self.normalAndSpecialPlayList = MakeNewRecordsData.normalAndSpecialPlayList;
          self.normalAddSpecialZodiacPlayList = MakeNewRecordsData.normalAddSpecialZodiacPlayList;
          /* 平码数据 */
          self.normalNumberPlayList = MakeNewRecordsData.normalNumberPlayList;
          /* 特码数据 */
          self.bigOrSmailList = MakeNewRecordsData.bigOrSmailList;
          self.tailBigOrSmailList = MakeNewRecordsData.tailBigOrSmailList;
          self.singleOrDoubleList = MakeNewRecordsData.singleOrDoubleList;
          self.familyOrWildList = MakeNewRecordsData.familyOrWildList;
          self.skyOrGroundList = MakeNewRecordsData.skyOrGroundList;
          self.colorList = MakeNewRecordsData.colorList;
          self.numberList = MakeNewRecordsData.numberList;
          self.zodiacList = MakeNewRecordsData.zodiacList;
          self.userName = '';
          self.userNameInputValue = '';
          self.userTotalMoney = '';
          self.userTotalMoneyInputValue = '';
          self.confirmDialogContext = '';

          // 初始化特码大小选择
          self.selectedBigOrSmail = '';
          self.selectedBigOrSmailEng = '';
          self.ifSelectedBigOrSmail = false;
          // 初始化特码单双选择
          self.selectedSingleOrDouble = '';
          self.selectedSingleOrDoubleEng = '';
          self.ifSelectedSingleOrDouble = false;
          // 初始化特码家野肖选择
          self.selectedFamilyOrWild = '';
          self.selectedFamilyOrWildEng = '';
          self.ifSelectedFamilyOrWild = false;
          // 初始化特码天地肖选择
          self.selectedSkyOrGround = '';
          self.selectedSkyOrGroundEng = '';
          self.ifSelectedSkyOrGround = false;
          // 初始化特码尾大小选择
          self.selectedTailBigOrSmail = '';
          self.selectedTailBigOrSmailEng = '';
          self.ifSelectedTailBigOrSmail = false;
          // 初始化特码波路选择
          self.selectedColor = '';
          self.selectedColorEng = '';
          self.ifSelectedColor = false;
          // 初始化生肖
          self.selectedZodiac = [];
          self.selectedZodiacEng = [];
          self.selectedNumbers = [];
          // 初始化付款选择
          self.selectedIfPaid = '已付款';
          // 初始化具体玩法
          self.selectedGameID = 0;
          self.selectedGameName = '';
          self.selectedNormalNumberGame = '';
          self.selectedNormalAndSpecialNumberGame = '';
          self.selectedNormalAddSpecialZodiacGame = '';
          // 移除组件
          self.pageRerender = false;
          self.currentSubTab = 0;
          self.getMoreUserNamelist();
          self.$refs['wxc-tab-page'].setPage(0, null, false);
          // 在组件移除后，重新渲染组件
          // this.$nextTick可实现在DOM 状态更新后，执行传入的方法。
          self.$nextTick(() => {
            self.pageRerender = true;
          });
        }
      });
      this.dialogShow = false;
    }
  }
};
</script>

<style scoped>
  .wrapper {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0
  }
  .item-minibar{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 100px;
  }
  .scroller {
    flex: 1;
    position: absolute;
    top: 100px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  /*通用文本*/
  .label-text {
    margin-top: 25px;
    margin-bottom: 0px;
    margin-left: 10px;
    color: grey;
  }
  .section-content {
    margin-top: 5px;
    height: 50px;
    margin-left: 15px;
    margin-right: 15px;
    flex-direction: row;
    align-items: baseline;
  }
  /*用户名字DIV布局*/
  .username-content {
    height: 50px;
    margin: 20px 15px;
    flex-direction: row;
     align-items: center;
     justify-content: center;
   }
  .username-button {
    width: 300px;
    height: 50px;
    max-width: 300px;
  }
  .lottery-issue-button {
    width: 150px;
    height: 50px;
    max-width: 150px;
    margin-left: 15px;
    margin-right: 15px;
  }
  .username-text {
    margin-right: 15px;
    margin-left: 15px;
    font-size: 20px;
  }
  .username-input {
    width: 300px;
    height: 50px;
    border-width: 1px;
    border-style: solid;
    border-color: rgb(203, 217, 207);
    padding-left: 20px;
    font-size: 28px;
  }
  /*玩法DIV布局*/
  .games-content-row {
    margin-top: 25px;
    height: 60px;
    margin-left: 40px;
    margin-right: 20px;
    flex-direction: row;
  }
  /*玩法DIV布局*/
  .games-content-number {
    margin-top: 20px;
    margin-left: 20px;
    margin-right: 20px;
    padding-bottom: 20px;
    padding-right: 20px;
    padding-left: 20px;
  }
  /*返点率div*/
  .rebate-content {
    margin-top: 50px;
    height: 50px;
    margin-left: 50px;
    margin-right: 50px;
    flex-direction: row;
    align-items: center;
    justify-content: left;
  }
  /*提交按钮DIV布局*/
  .submit-content {
    margin-top: 40px;
    height: 100px;
    margin-left: 15px;
    margin-right: 15px;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  .submit-button {
    width: 180px;
    height: 80px;
    max-width: 180px;
  }
</style>



