<template>
  <div class="wrapper">
    <wxc-tab-bar
      title-type="icon"
      :tab-titles="tabTitles"
      :tab-styles="tabStyles"
      @wxcTabBarCurrentTabSelected="wxcTabBarCurrentTabSelected">
      <div class="item-container" :style="contentStyle">
        <MakeNewRecords :globalIP="globalIP" :USERID="USERID" :lotteryIssueList="lotteryIssueList" :userNameList="userNameList"/>
      </div>
      <div class="item-container" :style="contentStyle">
        <Report :globalIP="globalIP" :USERID="USERID" :lotteryIssueList="lotteryIssueList"/>
      </div>
      <div class="item-container" :style="contentStyle">
        <UserCenter :globalIP="globalIP" :USERID="USERID" :lotteryIssueList="lotteryIssueList"/>
      </div>
    </wxc-tab-bar>
    <wxc-mask height="600"
              width="700"
              border-radius="0"
              duration="30000"
              mask-bg-color="#FFFFFF"
              :has-overlay="true"
              :show-close="false"
              :show="maskShow"
              :has-animation="false">
      <div class="mask-container">
        <div class="mask-title-container">
          <text class="mask-title">请输入邀请码</text>
        </div>
        <div class="mask-code-container">
          <input
              class="mask-input"
              type="text"
              @change="userInviteCodeChange"
              placeholder="验证码"
              :value="this.inviteCode">
        </div>
        <div class="mask-submit-button">
          <wxc-button
              text="提交"
              type="blue"
              size="medium"
              @wxcButtonClicked="submitCode"></wxc-button>
        </div>
      </div>
    </wxc-mask>
  </div>
</template>

<script>
import MakeNewRecords from './components/makeNewRecords/MakeNewRecords';
import Report from './components/report/Report';
import UserCenter from './components/userCenter/UserCenter';
import { WxcTabBar, WxcMask, WxcButton, Utils } from 'weex-ui';
import Config from './components/tabBarConfig';

// const SERVERIP = 'https://dry-river-39868.herokuapp.com/';
const SERVERIP = 'http://47.107.69.75:1337/';
// const USERID = 1;
// eslint-disable-next-line no-unused-vars
const USERMAP = [
  {
    CODE: 'root',
    USERID: 1
  },
  {
    CODE: 'alt1',
    USERID: 2
  },
  {
    CODE: 'bt2w',
    USERID: 3
  },
  {
    CODE: 'wwst',
    USERID: 4
  },
  {
    CODE: 'ttsa',
    USERID: 5
  },
  {
    CODE: 'lokt',
    USERID: 6
  }
];
const stream = weex.requireModule('stream') || {};
const modal = weex.requireModule('modal') || {};
export default {
  components: { WxcTabBar, MakeNewRecords, Report, UserCenter, WxcMask, WxcButton },
  name: 'App',
  data () {
    return {
      globalIP: SERVERIP,
      USERID: 0,
      // tabTitles: Config.tabIconFontTitles,
      // tabStyles: Config.tabIconFontStyles
      tabTitles: Config.tabTitles,
      tabStyles: Config.tabStyles,
      getLotteryIssueAPI: '', // 获取所有六合彩期数API
      userGuestAPI: '',
      lotteryIssueList: [],
      userNameList: [],
      maskShow: false,
      inviteCode: ''
    };
  },
  beforeCreate () {
  },
  created () {
    const tabPageHeight = Utils.env.getPageHeight();
    // const tabPageHeight = 1400;
    // 如果页面没有导航栏，可以用下面这个计算高度的方法
    // const tabPageHeight = Utils.env.deviceHeight / Utils.env.deviceWidth * 750;
    const { tabStyles } = this;
    this.contentStyle = { height: (tabPageHeight - tabStyles.height / 4) + 'px' };
    this.openNoAnimationMask();
    console.log('页面高度->', tabPageHeight);
    console.log('内容容器高度->', (tabPageHeight - tabStyles.height / 4) + 'px');
  },
  methods: {
    openNoAnimationMask (e) {
      this.maskShow = true;
    },
    submitCode () {
      const self = this;
      const userInfo = USERMAP.filter(item => (item.CODE === self.inviteCode));
      // const userInfo = [{
      //   CODE: 'alt1',
      //   USERID: 2
      // }];
      if (userInfo.length !== 0) {
        self.USERID = userInfo[0].USERID;
        self.getLotteryIssueList();
        self.getUserNamelist();
        self.maskShow = false;
      } else {
        modal.toast({
          message: '邀请码不正确！',
          duration: 1
        });
      }
    },
    userInviteCodeChange (e) {
      this.inviteCode = e.value;
      console.log('this.inviteCode->', this.inviteCode);
    },
    wxcTabBarCurrentTabSelected (e) {
      const index = e.page;
      console.log('当前页面->', index);
    },
    // 获取六合彩期数列表
    getLotteryIssueList () {
      const self = this;
      self.getLotteryIssueAPI = this.globalIP + 'dancecalendars';
      stream.fetch({
        method: 'GET',
        url: self.getLotteryIssueAPI,
        type: 'json',
        timeout: 30000
      }, function (response) {
        if (!response.ok) {
          self.lotteryIssueList = [];
          modal.toast({
            message: '服务器链接出问题了, 无法获取六合彩当前期数！',
            duration: 3
          });
        } else {
          self.lotteryIssueList = response.data.map(function (item) {
            let text = item.dancenumber;
            let key = item.key;
            return ({
              text: String(text),
              key: String(key)
            });
          });
          console.log('this.lotteryIssueList->', self.lotteryIssueList);
        }
      });
    },
    // 获取guest姓名列表
    getUserNamelist () {
      const self = this;
      self.userGuestAPI = this.globalIP + 'guests?user=' + self.USERID;
      console.log(self.userGuestAPI);
      stream.fetch({
        method: 'GET',
        url: self.userGuestAPI,
        type: 'json',
        timeout: 30000
      }, function (response) {
        if (!response.ok) {
          self.userNameList = [];
          modal.toast({
            message: '服务器链接出问题了, 无法获取历史玩家姓名！',
            duration: 3
          });
        } else {
          self.userNameList = response.data;
          console.log('this.userNameList->', self.userNameList);
        }
      });
    }
  }
};
</script>

<style scoped>
  .wrapper {
    justify-content: center;
    align-items: center;
  }
  .item-container {
    width: 750px;
    align-items: center;
    justify-content: center;
  }
  .mask-container {
    padding: 30px;
  }
  .mask-title-container {
    align-items: center;
    margin-top: 80px;
  }
  .mask-title {
    color: #333333;
    font-size: 40px;
  }
  .mask-code-container {
    align-items: center;
    margin-top: 100px;
  }
  .mask-input {
    width: 300px;
    height: 70px;
    border-width: 1px;
    border-style: solid;
    border-color: rgb(203, 217, 207);
    padding-left: 20px;
    font-size: 40px;
  }
  .mask-submit-button {
    align-items: center;
    margin-top: 100px;
  }
</style>



