<template>
  <div class="wrapper">
    <scroller class="scroller">
      <div class="item-minibar">
        <wxc-minibar
            :title="tabTitles[2].title"
            left-button=""
            background-color="#009ff0"
            text-color="#FFFFFF"></wxc-minibar>
      </div>
      <!--      <div class="user-info-container" :aria-hidden="true">-->
      <!--        <img class="user-image" alt="用户头像" src="https://gw.alicdn.com/tfs/TB1JpgJRFXXXXc7XpXXXXXXXXXX-800-800.png" />-->
      <!--        <text class="user-name">用户名</text>-->
      <!--      </div>-->
      <div class="lottery-content">
        <text class="lottery-text">第 </text>
        <wxc-button
            class="lottery-issue-button"
            :text="this.lotteryIssue === ''? '可选期数': this.lotteryIssue"
            type="white"
            size="medium"
            @wxcButtonClicked="selectLotteryIssueHandler('rebate-lotteryIssue')">
        </wxc-button>
        <text class="lottery-text"> 期</text>
        <!-- 期数 pop up -->
        <wxc-popover
            ref="rebate-lotteryIssue"
            :buttons="lotteryIssueList"
            :position="lotteryIssuePopoverPosition2"
            :arrowPosition="lotteryIssuePopoverArrowPosition2"
            @wxcPopoverButtonClicked="selectedLotteryIssue"
        ></wxc-popover>
      </div>
      <text class="category">设置佣金返点</text>
      <!--      <text>{{this.rebateRateList}}</text>-->
      <!--      <wxc-cell-->
      <!--          label="设置佣金返点"-->
      <!--          title=""-->
      <!--          :has-arrow="true"-->
      <!--          @wxcCellClicked="goToRebatePage('rebatePage')"-->
      <!--          :has-bottom-border="true">-->
      <!--      </wxc-cell>-->
      <!--      <router-view></router-view>-->
      <!--返点行-->
      <template v-for="(item) in this.rebateRateList">
        <!--        eslint-disable-next-line-->
        <div class="rebate-content">
          <text class="rebate-text">{{item !== undefined ? String(Object.keys(item)) : ''}}</text>
          <wxc-stepper
              :default-value="item !== undefined ? parseFloat((Object.values(item) * 100).toFixed(0)) : 0"
              step="1"
              max="100"
              min="0"
              @wxcStepperValueIsMinOver="rebateRateIsMinOver"
              @wxcStepperValueIsMaxOver="rebateRateIsMaxOver"
              @wxcStepperValueChanged="setRebateRate">
          </wxc-stepper>
          <wxc-button
              class="update-button"
              text="更新"
              type="blue"
              size="medium"
              @wxcButtonClicked="updateRebateRate(item)">
          </wxc-button>
        </div>
      </template>
    </scroller>
  </div>
</template>
<script>
import {
  WxcMinibar,
  WxcCell,
  WxcButton,
  WxcPopover,
  WxcStepper
} from 'weex-ui';
import Config from '../tabBarConfig';
// const dom = weex.requireModule('dom') || {}
const stream = weex.requireModule('stream') || {};
const modal = weex.requireModule('modal') || {};
// const USERID = 1;

export default {
  components: {
    WxcMinibar,
    WxcCell,
    WxcButton,
    WxcPopover,
    WxcStepper
  },
  props: {
    globalIP: {
      type: String,
      default: ''
    },
    USERID: {
      type: Number,
      default: 1
    },
    lotteryIssueList: {
      type: Array,
      // eslint-disable-next-line vue/require-valid-default-prop
      default: []
    }
  },
  data () {
    return {
      tabTitles: Config.tabIconFontTitles,
      selectedPath: '',
      getLotteryIssueAPI: this.globalIP + 'dancecalendars', // 获取所有六合彩期数API
      getUserRebatesAPI: this.globalIP + 'rebateusers/', // 获取所有六合彩期数API
      lotteryIssue: '',
      lotteryIssuePopoverPosition2: { x: 310, y: 180 },
      lotteryIssuePopoverArrowPosition2: { pos: 'top', x: 50 },
      rebateRateList: [],
      rebateRate: 0
    };
  },
  beforeCreate () {
  },
  mounted: function () {
    // this.getUserRebateList();
  },
  methods: {
    getUserRebateList () {
      const self = this;
      const selectedUserRebatesAPI = self.getUserRebatesAPI + '?dancenumber=' + this.lotteryIssue + '&user=' + self.USERID;
      console.log(selectedUserRebatesAPI);
      stream.fetch({
        method: 'GET',
        url: selectedUserRebatesAPI,
        type: 'json',
        timeout: 30000
      }, function (response) {
        if (!response.ok) {
          self.rebateRateList = [];
          modal.toast({
            message: '服务器链接出问题了！',
            duration: 3
          });
        } else {
          self.rebateRateList = response.data;
          console.log('this.rebateRateList->', self.rebateRateList);
        }
      });
    },
    selectLotteryIssueHandler (ref = 'rebate-lotteryIssue') {
      this.$refs[ref].wxcPopoverShow();
    },
    selectedLotteryIssue (obj) {
      console.log(obj);
      this.lotteryIssue = this.lotteryIssueList[obj.index].text;
      this.getUserRebateList();
    },
    setRebateRate (e) {
      this.rebateRate = e.value;
      console.log('设置返点率：', this.rebateRate);
    },
    updateRebateRate (item) {
      const self = this;
      let drinkname = String(Object.keys(item));
      let rate = parseFloat((this.rebateRate / 100).toFixed(2));
      const body = JSON.stringify(
        {
          'dancenumber': parseInt(this.lotteryIssue),
          'user': self.USERID,
          'drinkname': drinkname,
          'rate': rate
        });
      console.log('body->', body);
      stream.fetch({
        method: 'POST',
        url: self.getUserRebatesAPI,
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        type: 'json',
        body: body
      }, function (ret) {
        if (!ret.ok) {
          modal.toast({
            message: '网络出问题了！',
            duration: 3
          });
        } else {
          self.rebateRate = 0;
          modal.toast({
            message: '提交成功!',
            duration: 3
          });
          self.getUserRebateList();
        }
      });
    },
    rebateRateIsMaxOver () {
      modal.toast({
        message: '超过最大值啦'
      });
    },
    rebateRateIsMinOver () {
      modal.toast({
        message: '超过最小值啦'
      });
    },
    // 跳转返点率设置页面
    goToRebatePage (e) {
      console.log('click cell', e);
      this.$router.push(e);
      this.selectedPath = e;
    }
  }
};
</script>
<style scoped>
  .wrapper {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0
  }
  .scroller {
    flex: 1;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .item-minibar{
    top: 0;
    left: 0;
    right: 0;
    height: 100px;
  }
  .user-info-container {
    flex-direction: row;
    align-items: center;
  }
  .user-image {
    margin-left: 30px;
    width: 140px;
    height: 140px;
  }
  .user-name {
    font-size: 30px;
    font-weight: 600;
    color: #000000;
    margin-left: 30px;
  }
  .category {
    margin-top: 20px;
    padding-left: 25px;
    padding-top: 20px;
    width: 750px;
    height: 70px;
    background-color: #009ff0;
    justify-content: center;
    line-height: 0;
    font-size: 25px;
    color: #FFFFFF;
  }
  /*用户名字DIV布局*/
  .lottery-content {
    height: 50px;
    margin: 20px 15px;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  .lottery-text {
    margin-right: 15px;
    margin-left: 15px;
    font-size: 20px;
  }
  /*返点率div*/
  .rebate-content {
    margin-top: 20px;
    height: 60px;
    margin-left: 50px;
    margin-right: 50px;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  .lottery-text {
    margin-right: 15px;
    margin-left: 15px;
    font-size: 25px;
  }
  .rebate-text {
    margin-right: 15px;
    margin-left: 5px;
    font-size: 25px;
    position: absolute;
    left: 20px
  }
  .update-button {
    width: 150px;
    height: 60px;
    max-width: 150px;
    margin-left: 30px;
    margin-right: 15px;
    position: absolute;
    right: 40px
  }
</style>



